 


export default {
    get(name) {
        try {
           return  require('./' + name).default
        } catch (err) {
            return false
        }
       
    },
    getAll () {
        // const ignore = ['defaults', 'index']
        const collections = require.context('./', false, /^(?!.*(?:index.js$)).*\.js$/)
            .keys().map( file => ([
                file.replace('./', '').replace('.js',''), this.get(file.replace('./', ''))]) ) 
        return Object.fromEntries(collections)
    } 
}