

import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'
 

export default {
    // title: 'Transactions',
    title: {
        singular: 'Work Time',
        plural: 'Work Times',
    },
    icon: 'mdi-clock',

    items: {
     
        buttons: tableButtons.get('new', 'edit',  'delete'),
        filters: [



            {
                input: 'autocomplete',
                name: 'user',
                if: vm => vm.getPermissions('transaction').write.allOwn == 'all',
                col: {
                    sm: 4
                },
                bind: {
                    label: "User",
                    prependIcon: "mdi-account",
                    collection: 'user',
                    template: 'user',
                    itemText: "displayName",
                    itemValue: "_id",
                    query: {
                        role: ['agent', 'manager', 'owner']
                    },

                }
            },

            {
                input: 'date',
                name: 'start_date',
                type: 'gte',
                col: {
                    sm: 4
                },
                bind: {
                    label: "Start Date From",
                    prependIcon: "mdi-calendar",

                }
            },
            {
                input: 'date',
                name: 'end_date',
                type: 'lte',
                col: {
                    sm: 4
                },
                bind: {
                    label: "End Date To",
                    prependIcon: "mdi-calendar",

                }
            } 

        ],
        headers: [
            { 
                text: 'User', 
                sortable: false, 
                value: 'user.displayName',
                width: '200',
                component: () => import('./../components/items/fields/User.vue')
            },
            { text: 'profileImage', sortable: false, value: 'user.profileImage', hide: true },
            { text: 'Start Date', value: 'start_date', format: 'datetime' },
      
            { text: 'End Date', value: 'end_date', format: 'datetime' },
            { text: 'Total', value: 'amount', class: 'font-weight-bold'  },
         
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get('edit', 'delete', 'duplicate') }
        ]
    },
 
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar:{
            panels: [
    
                {
                    title: 'User',
                    icon: 'mdi-account',
                    fields: [
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.user.rights.filter(e => e.collection == 'transaction')[0].write.allOwn == 'all'),
                            // default: vm => vm.$store.state.auth.user._id,
                            bind: {
                                label: "Label",
                                // prependIcon: "mdi-account",
                                collection: 'user',
                                template: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                required: true,
                                or: false,
                               
                                rules: [
                                    v => !!v || 'Label is required',
                                ],
                            }
                        },
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Start Date Time',
                    icon: 'mdi-login',
                    fields:[
                        
               
                        {
                            input: 'time',
                            name: 'start_date',
                  
                            bind: {
                                label: "Start Time",
                                format: '24hr',
                                required: true,
                                rules: [
                                    v => !!v || 'Start Time is required',
                                ],
                            }
                        } 
                    ]
                },
                {
                    title: 'End Date Time',
                    icon: 'mdi-logout',
                    fields: [

                        {
                            input: 'time',
                            name: 'end_date',
                            bind: {
                                format: '24hr',
                                label: "End Time",
                                required: true,
                                rules: [
                                    v => !!v || 'End Time is required',
                                ],
                            }
                        }
                    ]
                }  
            ]
        }
    } 
}