import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
import itemButtons from './defaults/item_buttons'
import countries from '../country'

export default {
    // title: 'Banks',
    title: {
        singular: 'Bank',
        plural: 'Banks',
    },
    icon: 'mdi-bank',
    items: {
        buttons: tableButtons.get('new', 'edit', 'delete'),
        filters: [


            {
                input: 'autocomplete',
                name: '_id',
                col: {
                    sm: 6
                },
                bind: {
                    label: "Bank",
                    prependIcon: "mdi-bank",
                    collection: 'bank',
                    itemText: "number",
                    itemValue: "_id",

                }
            },
            {
                input: 'autocomplete',
                name: 'company',
                col: {
                    sm: 6
                },
                bind: {
                    label: "Company",
                    prependIcon: "mdi-briefcase",
                    collection: 'company',
                    itemText: "name",
                    itemValue: "_id",

                }
            },

        ],
        headers: [
            {
                text: 'Bank Number',
                align: 'start',
                value: 'number',
                sortable: false
            },
            {
                text: 'Company',
                align: 'start',
                value: 'company.name',
                sortable: false
            },
            { text: 'Country', value: 'country', sortable: false, format: 'country', width: '150'  },
            { text: 'Currency', value: 'currency', sortable: false, component: () => import('./../components/items/fields/Currency.vue')  },
            {
                text: 'Created By',
                sortable: false,
                value: 'user.displayName',
                width: '200',
                component: () => import('./../components/items/fields/User.vue')
            },
            { text: 'profileImage', sortable: false, value: 'user.profileImage', hide: true },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get('edit', 'delete', 'duplicate') }
        ]
    },
    item: {
        buttons: itemButtons.get('save', 'Cancel', 'spacer', 'delete'),
        sidebar: {
            panels:[
                {
                    title: 'Currency',
                    fields:[
                        {
                            input: 'select',
                            name: 'currency',
                            bind: {
                                label: "Bank Currency",
                                prependInnerIcon: "mdi-currency-usd-circle-outline",
                                items: ['USD', 'EUR', 'SAR', 'TRY', 'GBP', 'AED'],
                                required: true,
                                rules: [
                                    v => !!v || 'Currency is required',
                                ],
                            }
                        },
                    ]
                },
                {
                    title: 'Created By',
                    icon: "mdi-account",
                    fields:[
                        {
                            input: 'autocomplete',
                            name: 'user',
                            if: vm => (vm.user.rights.filter(e => e.collection == 'bank')[0].write.allOwn == 'all'),
                            default: vm => vm.user,
                            bind: {
                                label: "User",
                                template: 'user',
                                collection: 'user',
                                itemText: "displayName",
                                itemValue: "_id",
                                hideAddNew: true,
                            }
                        }
                    ]
                }
            ]
        },
        content: {
            panels: [
                {
                    title: 'Bank',
                    fields:[
                        {
                            input: 'text',
                            name: 'number',
                            bind: {
                                label: "Bank Number",
                                prependInnerIcon: 'mdi-card-account-details',
                                required: true,
                                rules: [
                                    v => !!v || 'ID is required',
                                ],
                            }
                        },
                        {
                            input: 'autocomplete',
                            name: 'company',
                            col: {
                                md: 6
                            },
                            bind: {
                                label: "Company",
                                prependInnerIcon: "mdi-briefcase",
                                collection: 'company',
                                itemText: "name",
                                itemValue: "_id",
                                required: true,
                                rules: [
                                    v => !!v || 'Bank is required',
                                ],
                            }
                        },
                        {
                            input: 'autocomplete',
                            name: 'country',
                            col: {
                                md: 6
                            },
                            bind: {
                                label: "Bank Country",
                                prependInnerIcon: "mdi-flag",
                                items: countries,
                                itemText: "name",
                                itemValue: "code",
                                required: true,
                                hideAddNew: true,
                                rules: [
                                    v => !!v || 'Country is required',
                                ],
                            }
                        },
                    ]
                }
            ]
        }
    }
}