import get from './getMethod'
export default {
    get,
    buttons: [
        {
            title: 'Edit',
            icon: 'mdi-pencil',
            if: (vm, item) => {


                const editOwn = vm.getPermissions(vm.$route.params.collection).update.allOwn == 'own'

                if (item.user && vm.canUpdate && editOwn) {
                    const userId = item.user instanceof Object && item.user._id ? item.user._id : item.user

                    return userId === vm.user._id
                } else
                    return vm.canUpdate
            },
            bind: {
                color: 'info'
            },
            on: (vm, item) => ({
                click () {
                    vm.$router.push(vm.$route.path + '/' + item._id)
                }
            })
        },
        {
            title: 'Delete',
            icon: 'mdi-delete',
            if: (vm, item) => {
                
             
                const deleteOwn = vm.getPermissions(vm.$route.params.collection).delete.allOwn == 'own'
               
                if (item.user && vm.canDelete && deleteOwn) {
                    const userId = item.user instanceof Object && item.user._id ? item.user._id : item.user
                    
                    return userId === vm.user._id
                } else 
                    return vm.canDelete
            },
            bind: {
                color: 'red'
            },
            on: (vm, item) => ({
                async click() {
                    const confirmed = await vm.$confirm('Are you sure do you want to delete this item ?', { title: 'Delete Item', color: 'red' })
                    if (confirmed) {
                        vm.loading = true
                        const rm = await vm.remove({ collection: vm.$route.params.collection, id: item._id })
                        if (rm.status === 200) {

                            vm.fetch()
                            vm.loading = false
                            vm.$store.commit('success', 'Item has been deleted successfuly')
                        }
                    }
                }
            })
        },
        {
            title: 'Duplicate',
            icon: 'mdi-content-copy',
            
            if: (vm, item) => {


                const writeOwn = vm.getPermissions(vm.$route.params.collection).write.allOwn == 'own'

                if (item.user && vm.canWrite && writeOwn) {
                    const userId = item.user instanceof Object && item.user._id ? item.user._id : item.user

                    return userId === vm.user._id
                } else
                    return vm.canWrite
            },
            on: (vm, item) => ({
                click() {
                    vm.$router.push(vm.$route.path + '/new?clone=' + item._id)
                }
            })
        },
        {
            title: 'Show in screen',
            icon: 'mdi-monitor-screenshot',

            if: (vm) => {
                    return vm.canRead && vm.getPermissions(vm.$route.params.collection).read.allOwn == 'all'
            },
            on: (vm, item) => ({
                async click() {
                    const res = await vm.get({ collection: vm.$route.path + '/notefy?id=' + item._id  })
                    if (res.status == 200) 
                        vm.$store.commit('success', 'Item has been shown to the screen')
                     
                }
            })
        }
    ]
}